import React, {Component} from 'react';
import {Icon, Form, Button, Radio, DatePicker, Input, Upload, InputNumber, Avatar, Divider, Modal, Select, Tag, TimePicker } from "antd";
import {connect} from "react-redux";
import {actionCreators} from '../store';
import moment from "moment";
import "./index.scss";
import MultiSelectModal from '../../../../common/components/SelectGonghao/MultiSelectModal';

const { Option } = Select;

class EditMessage extends Component {
  state = {
    selectGonghaoModal: true
  };

  componentWillMount() {
    this.props.getAllCodes();
    this.props.getAllGroup();
    let query = this.props.history.location.query;
    query && this.props.closeSelectModal();
    query && this.props.initKefuMessageInfo(query);
  }

  render() {
    const { kefuMessageInfo, content, changeContentValue, changeMessageValue, uploadImg, submitMessage, allCodes, changeGonghao, selectGonghaoModal, history, openSelectModal, closeSelectModal, openPreviewModal, closePreviewModal, openid, changeOpenId, previewModal, preview, allGroup, groupInputVisible, groupValue, showGroupInput, handleGroupInputChange, handleGroupInputConfirm, handleClose, cancelSelectGroup } = this.props;
    let isUploadImg, isTextUploadImg, FormItemWrapper;
    if (content.picurl) {
      isTextUploadImg = <img style={{width: "100px"}} src={content.picurl} alt=""/>
    } else {
      isTextUploadImg = <Button><Icon type="upload"/>  </Button>
    }
    if (kefuMessageInfo.img) {
      isUploadImg = <img style={{width: "100px"}} src={kefuMessageInfo.img} alt=""/>
    } else {
      isUploadImg = <Button><Icon type="upload"/>  </Button>
    }
    if (kefuMessageInfo.type === 1) {
      FormItemWrapper = <Form.Item label="" className="text-message">
        <Input.TextArea autosize={{minRows: 6}} value={content.description} placeholder="" onChange={e => changeContentValue(e, "description")}/>
      </Form.Item>
    } else if (kefuMessageInfo.type === 0) {
      FormItemWrapper = <div>
        <Form.Item label="">
          <Upload name="imageFile" action="/reply/upload" onChange={(info) => {
            uploadImg(kefuMessageInfo, info)
          }} showUploadList={false}>
            {isTextUploadImg}
          </Upload>
        </Form.Item>
        <Form.Item label="">
          <Input placeholder="" value={content.title} onChange={e => changeContentValue(e, "title")}/>
        </Form.Item>
        <Form.Item label="" className="text-message">
          <Input.TextArea autosize={{minRows: 6}} value={content.description} placeholder="" onChange={e => changeContentValue(e, "description")}/>
        </Form.Item>
        <Form.Item label="">
          <Input placeholder="" value={content.url} onChange={e => changeContentValue(e, "url")}/>
        </Form.Item>
      </div>
    } else if (kefuMessageInfo.type === 2) {
      FormItemWrapper =
        <Form.Item label="">
          <Upload name="imageFile" action="/reply/upload" onChange={(info) => {
            uploadImg(kefuMessageInfo, info)
          }} showUploadList={false}>
            {isUploadImg}
          </Upload>
        </Form.Item>;
    }
    const formItemLayout = {
      labelCol: {
        sm: {span: 4}
      },
      wrapperCol: {
        sm: {span: 20, offset: 0}
      },
    };
    let Channel;
    if(kefuMessageInfo.channel === 1) {
      Channel = <><Form.Item label="">
        <InputNumber value={kefuMessageInfo.delay} min={1} onChange={value => changeMessageValue(value, "delay")} />
        <Select value={kefuMessageInfo.isHour}  onChange={value => changeMessageValue(value, "isHour")} style={{width: 80}}>
          <Option value={0}></Option>
          <Option value={1}></Option>
        </Select>
      </Form.Item>
        <Form.Item label="">
          <Radio.Group value={kefuMessageInfo.action_type} onChange={e => changeMessageValue(e.target.value, "action_type")}>
            <Radio value={0}></Radio>
            <Radio value={1}></Radio>
            <Radio value={3}></Radio>
            <Radio value={2}></Radio>
          </Radio.Group>
        </Form.Item>
        </>
    } else if(kefuMessageInfo.channel === 2) {
      Channel =
        <Form.Item label="">
          <DatePicker showTime placeholder="" value={moment(kefuMessageInfo.timing_time)} onChange={(value, dateString) => changeMessageValue(dateString, 'timing_time')} />
        </Form.Item>
    } else if(kefuMessageInfo.channel === 3) {
      Channel =
        <Form.Item label="">
          <TimePicker placeholder="" value={moment(kefuMessageInfo.daily_time)} onChange={(value, dateString) => changeMessageValue(value, 'daily_time')} />
        </Form.Item>
    }
    return (
      <div>
        <div className="container edit-message">
          {/*{isImgAndText}*/}
          <div className="kefu-form fl">
            <Form {...formItemLayout}>
              <Form.Item label="">
                <Avatar onClick={openSelectModal} className="cursor" shape="square" icon="user" />
              </Form.Item>
              <Form.Item label="">
                <Input value={kefuMessageInfo.remarks} placeholder=",()" onChange={e => changeMessageValue(e.target.value, "remarks")} />
              </Form.Item>
              <Form.Item label="">
                <Tag color={kefuMessageInfo.group !== "" ? "#1890ff" : ""} closable={kefuMessageInfo.group !== ""} onClose={cancelSelectGroup} style={kefuMessageInfo.group !== "" ? {} : { background: '#fff', borderStyle: 'dashed' }}>
                 {kefuMessageInfo.group}
                </Tag>
              </Form.Item>
              <Form.Item label="">
                {groupInputVisible && (
                  <Input
                    ref={input => this.input = input}
                    type="text"
                    size="small"
                    style={{ width: 78, marginRight: 10 }}
                    value={groupValue}
                    onChange={handleGroupInputChange}
                    onBlur={handleGroupInputConfirm}
                    onPressEnter={handleGroupInputConfirm}
                  />
                )}
                {!groupInputVisible && (
                  <Tag className="cursor" onClick={() => showGroupInput(this)} style={{ background: '#fff', borderStyle: 'dashed' }}>
                    <Icon type="plus" /> New Tag
                  </Tag>
                )}
                {allGroup.map((item, index) => <Tag color="#1890ff" key={item._id} className="cursor" closable onClose={e => handleClose(e, item._id, index)} onClick={e => changeMessageValue(item.group, "group")}>
                  {item.group}
                </Tag>)}
              </Form.Item>
              <Divider style={{width: "205%", marginLeft: -50}} />
              <h2 className="block-title"></h2>
              <Form.Item>
                <Radio.Group buttonStyle="solid" value={kefuMessageInfo.type} onChange={e => changeMessageValue(e.target.value, "type")}>
                  <Radio.Button value={0}></Radio.Button>
                  <Radio.Button value={1}></Radio.Button>
                  <Radio.Button value={2}></Radio.Button>
                </Radio.Group>
              </Form.Item>
              {FormItemWrapper}
              <Divider style={{width: "175%", marginLeft: -50}} />
              <h2 className="block-title"></h2>
              <Form.Item label="">
                <Radio.Group value={kefuMessageInfo.sex} onChange={e => changeMessageValue(e.target.value, "sex")}>
                  <Radio value="all"></Radio>
                  <br/>
                  <Radio value="1"></Radio>
                  <Radio value="2"></Radio>
                  <Radio value="0"></Radio>
                </Radio.Group>
              </Form.Item>
              <Divider style={{width: "175%", marginLeft: -50}} />
              <h2 className="block-title"></h2>
              <Form.Item label="">
                <Radio.Group value={kefuMessageInfo.channel} onChange={e => changeMessageValue(e.target.value, "channel")}>
                  <Radio.Button value={0}></Radio.Button>
                  <Radio.Button value={1}></Radio.Button>
                  <Radio.Button value={2}></Radio.Button>
                  <Radio.Button value={3}></Radio.Button>
                </Radio.Group>
              </Form.Item>
              {Channel}
              <Divider style={{width: "175%", marginLeft: -50}} />
              <Form.Item>
                <Button size="large" type="primary" onClick={() => submitMessage(kefuMessageInfo, history, allCodes)}>{!kefuMessageInfo._id ? "" : ""}</Button>
                {kefuMessageInfo._id && <Button style={{marginLeft: 20}} type="info" size="large" onClick={() => history.goBack()}></Button>}
                <Button size="large" style={{marginLeft: 30, background: "yellowgreen", borderColor: "yellowgreen"}} type="primary" onClick={openPreviewModal}></Button>
              </Form.Item>
            </Form>
          </div>
        </div>
        {selectGonghaoModal && <MultiSelectModal allCodes={allCodes} selectGonghaoModal={selectGonghaoModal} changeGonghao={changeGonghao} closeSelectModal={closeSelectModal} codes={kefuMessageInfo.codes} />}
        <Modal title="" visible={previewModal} onCancel={closePreviewModal} onOk={() => preview(kefuMessageInfo, openid)}>
          <Input onChange={changeOpenId} value={openid} placeholder="openid"/>
          <span className="tips">Tips: <span> openid </span>,<br/>openid</span>
        </Modal>
      </div>
    )
  }

  componentWillUnmount() {
    this.props.resetState()
  }
}

const mapState = state => ({
  kefuMessageInfo: state.message.kefu.kefuMessageInfo,
  content: state.message.kefu.content,
  allCodes: state.message.kefu.allCodes,
  selectGonghaoModal: state.message.kefu.selectGonghaoModal,
  previewModal: state.message.kefu.previewModal,
  openid: state.message.kefu.openid,
  allGroup: state.message.kefu.allGroup,
  groupInputVisible: state.message.kefu.groupInputVisible,
  groupValue: state.message.kefu.groupValue,
});

const mapDispatch = dispatch => ({
  initKefuMessageInfo(query) {
    dispatch(actionCreators.initKefuMessageInfo(query));
  },
  changeContentValue(e, name) {
    dispatch(actionCreators.changeContentValue(e.target.value, name));
  },
  uploadImg(kefuMessageInfo, info) {
    if (info.file.status === 'done') {
      let img = `http://t.dmmup.com//uploads/${info.file.response.filename}`,
        img_path = `./public/uploads/${info.file.response.filename}`;
      if (kefuMessageInfo.type === 0) {
        dispatch(actionCreators.changeContentValue(img, "picurl"));
      } else {
        dispatch(actionCreators.changeMessageValue(img, "img"));
        dispatch(actionCreators.changeMessageValue(img_path, "img_path"));
      }
    }
  },
  changeMessageValue(value, name) {
    dispatch(actionCreators.changeMessageValue(value, name));
  },
  resetState() {
    dispatch(actionCreators.resetState());
  },
  selectParams() {
    dispatch(actionCreators.selectParams());
  },
  submitMessage(info, history, allCodes) {
    dispatch(actionCreators.submitMessage(info, history, allCodes));
  },
  changeGonghao(codes) {
    dispatch(actionCreators.changeGonghao(codes))
  },
  getAllCodes() {
    dispatch(actionCreators.getAllCodes())
  },
  openSelectModal() {
    dispatch(actionCreators.openSelectModal())
  },
  closeSelectModal() {
    dispatch(actionCreators.closeSelectModal())
  },
  changeOpenId(e) {
    dispatch(actionCreators.changeOpenId(e.target.value))
  },
  openPreviewModal() {
    dispatch(actionCreators.openPreviewModal())
  },
  closePreviewModal() {
    dispatch(actionCreators.closePreviewModal())
  },
  preview(info, openid) {
    dispatch(actionCreators.preview(info, openid))
  },
  getAllGroup() {
    dispatch(actionCreators.getAllGroup())
  },
  showGroupInput(that) {
    dispatch(actionCreators.showGroupInput(that))
  },
  handleGroupInputChange(e) {
    dispatch(actionCreators.handleGroupInputChange(e.target.value))
  },
  handleGroupInputConfirm(e) {
    dispatch(actionCreators.handleGroupInputConfirm(e.target.value))
  },
  handleClose(e, _id, index) {
    e.preventDefault();
    e.stopPropagation();
    dispatch(actionCreators.handleClose(_id, index))
  },
  cancelSelectGroup(e) {
    e.preventDefault();
    dispatch(actionCreators.cancelSelectGroup())
  }
});

export default connect(mapState, mapDispatch)(EditMessage);